import pageHeader from '../home/pageHeader.vue'
import interfaces from '../../../utils/interfaces.js'
export default {
	//组件注册
	components:{
		pageHeader
	},
	data() {
		return {
			categoryList: [],
			showCategoryIndex: 0//获取下标的
		}
	},
	onLoad() {
	  this.initDate()
	},
	methods: {
		// 获取接口数据的方法
			initDate(){
			this.request({
			    url: interfaces.getCategory,
			    success: ((res) => {
					console.log(res,'分类')
					this.categoryList = res.data
				})
			})
		},
		//左侧点击事件
		handleCategory(index) {
			this.showCategoryIndex = index
		},
		// 点击右侧的每一个小图片
		handleCategoryList(row) {
			console.log(row)
			uni.navigateTo({
				url:'../../goods/goodsList?name='+row.name
			})
		}
	}
}
